<template>
	<view>
		<view class="profile-view-navbar">
			<uni-nav-bar>
				<block slot="left">
					<view class="profile-view-refresh">
						<uni-icons fontFamily="eosfont" :size="26" color="#636363">{{'&#xe61e;'}}</uni-icons>
					</view>
				</block>
				<view class="profile-view-navbar-center">
					<view class="profile-view-navbar-title">
						{{gameName}}
					</view>
				</view>
		
				<block slot="right">
					<view class="profile-view-money">
						<uni-icons fontFamily="eosfont" :size="26" color="#636363">{{'&#xe658;'}}</uni-icons>
					</view>
				</block>
			</uni-nav-bar>
		</view>
	</view>
</template>

<script>
	import {
		rq
	} from '../../utils/api.js'
	export default {
		data() {
			return {
				gameName: '游戏名称'
			}
		},
		methods: {

		},
		//读取参数 设置标题
		async onLoad(options) {
			const gameid = options.id
			const res = await rq({
				url: `/games/${gameid}`,
				method: 'GET'
			})
			this.gameName = res.name
			
		}
	}
</script>

